import React, { useState, useEffect } from 'react'
import {
  Container,
  Row,
  Col,
  Button
} from 'reactstrap'
import SubTitle from '../../components/SubTitle'
import UserStatusCard from '../../components/UserStatusCard'
import { Offline, Online } from "react-detect-offline"
import classnames from 'classnames'

// ** Firebase
import { createGame } from '@services/firebase'

// ** Utility
import { isMetaMaskInstalled, switchToHPBNetwork } from '@utility/metamask'
import { auth as fireAuth } from '@utility/firebase'

// ** Use Wallet
import { useWallet } from 'use-wallet'
import TokenAmount from 'token-amount'

// ** Contracts
import ESRTokenContract from '@contracts/ESRTokenContract'

// ** Contract Interacts
import { generateNewHPBGameContract } from '@services/factory-hpb-interacts'

// ** Store & Actions
import { useSelector, useDispatch } from 'react-redux'
import { handleUpdateUser, handleOnlineStatus } from '@store/actions/auth'

const Compete = () => {
  const auth = useSelector(state => state.auth)
  const wallet = useWallet()

  const [hpbBalance, setHPBBalance] = useState(0)
  const [esrBalance, setESRBalance] = useState(0)
  const [userId, setUserId] = useState('')

  // message for new contract url
  const SUCCESS = 200, FAILURE = 400
  const [message, setMessage] = useState({
    status: undefined,
    tokenType: '',
    tokenContractAddress: '',
    message: ''
  })

  useEffect(() => {
    if (isMetaMaskInstalled()) {
      const { balance } = wallet
      if (parseInt(balance) === -1) {
        switchToHPBNetwork().then(res => {
          if (res && res.status === 200) {
            setTimeout(() => wallet.connect('injected'), 500)
          }
        })
      }
    } else {
      alert('Please install MetaMask first.')
    }
  }, [])

  useEffect(() => {
    const { balance, account } = wallet
    setHPBBalance(TokenAmount.format(balance, 18, { symbol: 'xDai' }))

    if (account) {
      ESRTokenContract.methods.balanceOf(account).call().then(_esrBalance => {
        setESRBalance(TokenAmount.format(_esrBalance, 18, { symbol: 'ESR' }))
      })
    } else {
      setESRBalance('0 ESR')
    }
  }, [wallet])

  // set user id
  useEffect(() => {
    setUserId(fireAuth.currentUser?.uid)
  }, [fireAuth])

  /**
   * Generate new HPB game contract
   */
  const handleGenerateHPBGameContract = async () => {
    const res = await generateNewHPBGameContract(wallet.account)
    if (res.success) {
      setMessage({
        status: SUCCESS,
        tokenType: 'xDai',
        tokenContractAddress: res.generatedContractAddress
      })
      createGame({
        player1: userId,
        status: 'created',
        tokenType: 'xDai',
        tokenContractAddress: res.generatedContractAddress
      })
    } else {
      setMessage({
        status: FAILURE,
        message: res.message
      })
    }
  }

  return (
    <div className="content-bodyd">
      <SubTitle title={`Welcome, ${auth.user.fullname.split(' ')[0]}`} className="m-t-40" />
      <Container>
        <Online>
          <Row className=''>
            <Col md={4} className='flex font-16 mx-2 mx-md-0 order-2 order-md-1'>
              <div>
                <div className=''><strong>Gnosis Wallet Address: </strong><span className='text-break'>{wallet.account}</span></div>
                <div className='mt-1'><strong>MetaMask: </strong>{wallet.status}</div>
                <div className='mt-1'><strong>Network name: </strong>{wallet.chainId === 100 ? 'Gnosis' : wallet.networkName}</div>
                <div className='mt-1'><strong>xDai Coin Balance: </strong>{hpbBalance}</div>
                <div className='mt-1'><strong>ESR Token Balance: </strong>{esrBalance}</div>
                <div className='mt-1'><strong>KUDOS Token Balance: </strong>{0}</div>
              </div>
            </Col>
            <Col md={4} className='flex align-items-center my-2 my-md-0 order-2 order-md-2'>
              <Container className='px-2'>
                <Button color='primary' block onClick={handleGenerateHPBGameContract}>Generate new xDai Game contract</Button>
                <Button color='success my-1' block>Generate new ESR Game contract</Button>
                <Button color='warning' block>Generate new KUDOS Game contract</Button>
              </Container>
            </Col>
            <Col md={4} className='flex justify-content-center align-items-center order-1 order-md-3'>
              <div className='' style={{ width: 180 }}>
                <UserStatusCard {...auth.user} />
              </div>
            </Col>
          </Row>
          <Row className='flex justify-content-center mx-2 my-md-2'>
            <Col md={4} className='px-0'>
              <Button color='primary' block>xDai Game contract list</Button>
            </Col>
            <Col md={4} className='px-0 px-md-2'>
              <Button color='success my-1 my-md-0' block>ESR Game contract list</Button>
            </Col>
            <Col md={4} className='px-0'>
              <Button color='warning' block>KUDOS Game contract list</Button>
            </Col>
          </Row>
          <Row className={classnames('mt-3', {
            'd-none': message.status === undefined
          })}>
            <Col md={{ size: 10, offset: 1 }} >
              <div className='text-white py-3 rounded text-center border'>
                {message.status === SUCCESS && (
                  <>
                    <h3>Congratulations!</h3>
                    <p className='mt-1 mb-0'>
                      Click the url to visit your new {message.tokenType} game contract: {' '}
                      <a className='text-success' href={`/${message.tokenType}/${message.tokenContractAddress}`}>
                        {message.tokenContractAddress}
                      </a>
                    </p>
                  </>
                )}
                {message.status === FAILURE && (
                  <>
                    <h3>Woops! Something went wrong</h3>
                    <p className='mt-1 mb-0'>{message.message}</p>
                  </>
                )}
              </div>
            </Col>
          </Row>
        </Online>
        <Offline>
          <div className='flex justify-content-center' style={{ minHeight: '28vh' }}>
            <div className="h3">Internet was disconnected, please try again when it's available</div>
          </div>
        </Offline>
      </Container>
    </div>
  )
}

export default Compete
