// ** Contracts
import FactoryHPBContract from '@contracts/FactoryHPBContract'

/**
 * Returns address created
 * 
 * @param {string} account Account address
 * @returns {object} success: { success: true, generatedContractAddress }, failure: { success: false, message }
 */
const generateNewHPBGameContract = async (account) => {
  let resObj = {}
  
  try {
    const createdContract = await FactoryHPBContract.methods.spawnNewGameContract().send({
      from: account
    })

    resObj = {
      success: true,
      generatedContractAddress: createdContract.events.newContractAddress.returnValues[0]
    }
  } catch (error) {
    resObj = {
      success: false,
      message: error.message
    }
  }

  return resObj
}

export { generateNewHPBGameContract }